/* Add your existing CSS styles here */
.containe-fluid {
  display: flex;
  justify-content: auto; /* Center the divs horizontally */

}

.slid-container {
  display: flex;
  flex-wrap: nowrap;
  /* This prevents wrapping into multiple columns */
}

.slid {
  flex: 0 0 399px;
  margin-right: 50px; /* Add the desired gap between divs */
}

/* Add this CSS style to display the divs in a single line */
.dz-box {
  display: flex;

}

/* Add the class to apply up and down effect */
.dz-box.alternate:nth-child(odd) .dz-info {
  transform: translateY(-10px); /* Move odd divs up by 10 pixels */
}

.dz-box.alternate:nth-child(even) .dz-info {
  transform: translateY(10px); /* Move even divs down by 10 pixels */
}

.slider {
  width: 100%;
  overflow: hidden;
}

.slides-container {
  display: flex;
  animation: slideAnimation 20s linear infinite;
}

.slide {
 
  flex-shrink: 0; /* Prevent slides from shrinking and preserve their width */
  margin-right: 12px;
  width:15%;
}

/* CSS animation */
@keyframes slideAnimation {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100%);
  }
}

/* CSS animation */
@keyframes slideAnimation {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-100%);
  }
}

/* CSS animation */
@keyframes slideAnimation {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100%);
  }
}


.prev,
.next {
  padding: 10px 20px;
  background-color: #f2f2f2;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.prev:hover,
