.loading-page-3 .loading-area .loader {
  -webkit-animation: cube-spin 1.5s ease-in-out infinite alternate;
  animation: cube-spin 1.5s ease-in-out infinite alternate;
  height: 50px;
  margin: 50px auto 0;
  position: relative;
  -webkit-transform: rotatex(30deg) rotatey(45deg);
  transform: rotatex(30deg) rotatey(45deg);
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
  width: 50px;
}
.loading-page-3 .loading-area .loader .side {
  bottom: 0;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
}
.loading-page-3 .loading-area .loader .side:before {
  -webkit-animation: cube-explode 1.5s ease-in-out infinite;
  animation: cube-explode 1.5s ease-in-out infinite;
  background: rgba(163, 204, 2, 0.9);
  background: var(--rgba-primary-9);
  bottom: 0;
  content: "";
  left: 0;
  opacity: 0.5;
  position: absolute;
  right: 0;
  top: 0;
  -webkit-transform: translatez(1.7em);
  transform: translatez(1.7em);
}
.loading-page-3 .loading-area .loader .side:first-child {
  -webkit-transform: rotatey(90deg);
  transform: rotatey(90deg);
}
.loading-page-3 .loading-area .loader .side:nth-child(2) {
  -webkit-transform: rotatey(180deg);
  transform: rotatey(180deg);
}
.loading-page-3 .loading-area .loader .side:nth-child(3) {
  -webkit-transform: rotatey(270deg);
  transform: rotatey(270deg);
}
.loading-page-3 .loading-area .loader .side:nth-child(4) {
  -webkit-transform: rotatey(1turn);
  transform: rotatey(1turn);
}
.loading-page-3 .loading-area .loader .side:nth-child(5) {
  -webkit-transform: rotatex(90deg);
  transform: rotatex(90deg);
}
.loading-page-3 .loading-area .loader .side:nth-child(6) {
  -webkit-transform: rotatex(270deg);
  transform: rotatex(270deg);
}
@-webkit-keyframes cube-spin {
  0% {
    -webkit-transform: rotatex(30deg) rotatey(45deg);
    transform: rotatex(30deg) rotatey(45deg);
  }
  to {
    -webkit-transform: rotatex(30deg) rotatey(405deg);
    transform: rotatex(30deg) rotatey(405deg);
